import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Divider from '@material-ui/core/Divider';
import BuildIcon from '@material-ui/icons/Build';
import QueryBuilderIcon from '@material-ui/icons/QueryBuilder';
import { TextField, InputAdornment, OutlinedInput, Paper, Typography, Tooltip, Card } from '@material-ui/core';
import Chip from '@material-ui/core/Chip';
import LabelIcon from '@material-ui/icons/Label';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import Checkbox from '@material-ui/core/Checkbox';
import RequestCard from '../../../components/RequestCard';
import Slide from '@material-ui/core/Slide';

const useStyles = makeStyles((theme) => ({
    rootList: {
        marginTop: theme.spacing(5),
        width: '100%',
        maxWidth: 500,
        margin: 'auto',
    },
    backgroundStyle: {
        backgroundColor: 'white',
    }
}));

const categories = [
    {
        id: 0,
        value: ' '
    },
    {
        id: 1,
        value: ' '
    },
    {
        id: 2,
        value: ' '
    },
    {
        id: 3,
        value: ''
    }
];

const deadLines = [
    {
        id: 0,
        value: '1'
    },
    {
        id: 1,
        value: '3'
    },
    {
        id: 2,
        value: '6'
    },
    {
        id: 3,
        value: '12'
    },
    {
        id: 4,
        value: '18'
    },
    {
        id: 5,
        value: '24'
    }
];

function EnrollList({ stepIndex, handleEnrollData }) {

    const classes = useStyles();

    const [categoryInput, setCategoryInput] = useState({
        category: '',
        categoryIndex: null,
    });

    const { category, categoryIndex } = categoryInput;

    const [deadLineInput, setDeadLineInput] = useState({
        deadLine: '',
        deadLineIndex: null,
    });

    const { deadLine, deadLineIndex } = deadLineInput;

    const [hopeDateInput, setHopeDateInput] = useState({
        hopeDate: '',
        hopeDateIndex: null,
    });

    const { hopeDate, hopeDateIndex } = hopeDateInput;

    const onChangeTime = (index, value) => {
        const now = new Date()
        let str = value.slice(0, -2);
        let end = now.setHours(now.getHours() + Number(str));
        setDeadLineInput({
            deadLine: end,
            deadLineIndex: index,
        });
        handleEnrollData('deadLine', end)
    }

    const onChangeCategory = (index, value) => {
        setCategoryInput({
            category: value,
            categoryIndex: index
        });
        handleEnrollData('category', value)
    };

    const onChangeHopeDate = (index, value) => {
        setHopeDateInput({
            hopeDate: value,
            hopeDateIndex: index
        });
        handleEnrollData('hopeDate', value)
    };


    const [tag, setTag] = useState('');

    const [tagList, setTagList] = useState([]);

    const onChangeTag = (e) => {
        setTag(e.target.value);
    }

    const onSubmitForm = (e) => {
        e.preventDefault();
        if (tag === '') {
            return;
        } else {
            setTagList([
                ...tagList,
                tag
            ])
            handleEnrollData('tags', [
                ...tagList,
                tag
            ])
            setTag('');
        }
    }

    const onClickRemove = (index) => {
        const list = tagList.filter((obj, x) => {
            return x !== index;
        })
        setTagList(list)
    }

    const showTagList = tagList.map((obj, index) => {
        return <span key={index}><Chip label={obj} variant="outlined" size="small" onDelete={() => { onClickRemove(index) }} />&nbsp;</span>
    })

    const [detail, setDetail] = useState('');

    const onChangeDetail = (e) => {
        setDetail(e.target.value);
        handleEnrollData('detail', e.target.value);
    }


    const printList = (arr, index, fnc) => {
        return (
            <Paper elevation={3}>
                <List className={classes.backgroundStyle} component="nav" aria-label="main mailbox folders">
                    {arr.map((obj) => {
                        return (
                            <div key={obj.id}>
                                <Slide timeout={100 * obj.id} direction="left" in={true} mountOnEnter unmountOnExit>
                                    <ListItem button selected={index === obj.id} onClick={() => fnc(obj.id, obj.value)}>
                                        <ListItemIcon>
                                            <Checkbox
                                                checked={index === obj.id}
                                            />
                                        </ListItemIcon>
                                        <ListItemText primary={obj.value} />
                                    </ListItem>
                                </Slide>
                            </div>
                        )
                    })}
                </List>
            </Paper>
        )
    }

    switch (stepIndex) {
        case 0:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4"><BuildIcon />   ?</Typography>
                    <br />
                    {category}
                    <br /><br />
                    {printList(categories, categoryIndex, onChangeCategory)}
                </div>
            )
        case 1:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4" gutterBottom><QueryBuilderIcon />   .</Typography>
                    <Typography paragraph>
                             !
                    </Typography>
                    {deadLine === '' ? <></> : <p>  : {new Date(deadLine).toLocaleString()}</p>}
                    {printList(deadLines, deadLineIndex, onChangeTime)}
                </div>
            )
        case 2:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4" gutterBottom><CalendarTodayIcon />    .</Typography>
                    <Typography paragraph>{hopeDate}</Typography>
                    <Typography paragraph>
                          !
                    </Typography>
                    <Paper elevation={3}>
                        <List className={classes.backgroundStyle} component="nav" aria-label="main mailbox folders">
                            <Slide timeout={200} direction="left" in={true} mountOnEnter unmountOnExit>
                                <ListItem button selected={hopeDateIndex === 0} onClick={() => onChangeHopeDate(0, ' ')}>
                                    <ListItemIcon>
                                        <Checkbox
                                            checked={hopeDateIndex === 0}
                                        />
                                    </ListItemIcon>
                                    <ListItemText primary=' ' />
                                </ListItem>
                            </Slide>
                            <Slide timeout={300} direction="left" in={true} mountOnEnter unmountOnExit>
                                <ListItem button selected={hopeDateIndex === 1} onClick={() => onChangeHopeDate(1)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            checked={hopeDateIndex === 1}
                                        />
                                    </ListItemIcon>
                                    <ListItemText primary=' ' />
                                    {hopeDateIndex === 1
                                        ? <TextField
                                            id="date"
                                            label="date"
                                            type="date"
                                            defaultValue={new Date().toDateString()}
                                            className={classes.textField}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            onChange={(e) => {
                                                if (new Date(e.target.value) < new Date()) {
                                                    alert('  .   ')
                                                } else {
                                                    onChangeHopeDate(1, e.target.value)
                                                }
                                            }}
                                        />
                                        : <></>
                                    }
                                </ListItem>
                            </Slide>
                        </List>
                    </Paper>
                </div>
            )
        case 3:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4" gutterBottom><LabelIcon />  ?</Typography>
                    <Typography paragraph>
                           !<br />
                            .
                    </Typography>
                     : <Chip label="" variant="outlined" size="small" />&nbsp;
                    <Chip label="" variant="outlined" size="small" />&nbsp;
                    <Chip label="" variant="outlined" size="small" />&nbsp;
                    <Chip label="IOS" variant="outlined" size="small" />
                    <br />
                    <br />
                    <Slide direction="left" in={true} mountOnEnter unmountOnExit>
                        <form onSubmit={onSubmitForm}>
                            <OutlinedInput
                                fullWidth
                                style={{ backgroundColor: 'white' }}
                                size="small"
                                placeholder="  !"
                                value={tag}
                                autoFocus
                                endAdornment={<InputAdornment position="end">ENTER</InputAdornment>}
                                onChange={onChangeTag} />
                            <br /><br />
                        </form>
                    </Slide>
                    {showTagList}<br /><br />
                </div>
            )
        case 4:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4" gutterBottom><AddCircleOutlineIcon />     !</Typography>
                    <Typography paragraph>
                            .
                        <br />
                            .
                    </Typography>
                    <Slide timeout={200} direction="left" in={true} mountOnEnter unmountOnExit>
                        <TextField id="outlined-multiline-static"
                            label="    !"
                            style={{ backgroundColor: 'white' }}
                            multiline
                            rows={5}
                            variant="outlined"
                            fullWidth
                            value={detail}
                            onChange={onChangeDetail}
                        />
                    </Slide>
                </div>
            )
        case 5:
            return (
                <div className={classes.rootList}>
                    <Typography variant="h4" gutterBottom>  !</Typography>
                    <br />
                    <Card elevation={3}>
                        <List>
                            {[
                                {
                                    icon: () => <BuildIcon />,
                                    title: '',
                                    text: category !== '' ? category : ' .'
                                },
                                {
                                    icon: () => <QueryBuilderIcon />,
                                    title: ' ',
                                    text: deadLine !== '' ? new Date(deadLine).toLocaleString() : ' .'
                                },
                                {
                                    icon: () => <AddCircleOutlineIcon />,
                                    title: '',
                                    text: detail !== '' ? detail : ' .'
                                },
                                {
                                    icon: () => <LabelIcon />,
                                    title: '',
                                    text: tagList.map((obj) => {
                                        return <span key={obj}><Chip variant="outlined" size="small" label={obj} />&nbsp;</span>
                                    })
                                },
                                {
                                    icon: () => <CalendarTodayIcon />,
                                    title: '  ',
                                    text: hopeDate
                                },
                            ].map((obj, index) => {
                                return (
                                    <ListItem key={index}>
                                        <ListItemIcon>
                                            <Tooltip arrow title={obj.title}>
                                                {obj.icon()}
                                            </Tooltip>
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={obj.text}
                                        />
                                    </ListItem>
                                )
                            })}
                        </List>
                    </Card>
                </div>

            )
        default:
            return 'Unknown stepIndex';
    }
}

export default EnrollList;